import React, { useRef, useState } from 'react';
import ForceGraph2D from 'react-force-graph-2d';
import { graphApi } from '../../services/api';
import { logger } from '../../services/logger';

const Graph = ({ onGraphSelect }) => {
  const [graphData, setGraphData] = useState({ nodes: [], links: [] });
  const graphRef = useRef();

  // Завантаження графа при виборі
  React.useEffect(() => {
    const loadGraph = async (graphId) => {
      try {
        const data = await graphApi.getGraph(graphId);
        if (data) {
          const formattedData = {
            nodes: data.nodes.map(node => ({
              id: node.id,
              x: node.x,
              y: node.y
            })),
            links: data.edges.map(edge => ({
              source: edge.source,
              target: edge.target,
              weight: edge.weight
            }))
          };
          setGraphData(formattedData);
          logger.info('Граф завантажено успішно', { graphId });
        }
      } catch (error) {
        logger.error('Помилка при завантаженні графа', error);
      }
    };

    if (onGraphSelect) {
      loadGraph(onGraphSelect);
    }
  }, [onGraphSelect]);

  return (
    <div className="bg-white shadow rounded-lg p-4">
      <h2 className="text-xl font-semibold mb-4">Візуалізація графа</h2>
      <div className="h-[600px] border rounded-lg">
        <ForceGraph2D
          ref={graphRef}
          graphData={graphData}
          nodeLabel="id"
          nodeColor={() => '#1a56db'}
          linkColor={() => '#e5e7eb'}
          nodeRelSize={6}
          linkWidth={1}
          linkDirectionalParticles={2}
          linkDirectionalParticleSpeed={0.005}
        />
      </div>
    </div>
  );
};

export default Graph; 